@use '../global' as *;

/*  box sizing */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/*    */
ul,
ol {
  padding: 0;
}

/*    */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

/*    -  body */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/*      ul  il,     class*/
ul[class],
ol[class] {
  list-style: none;
}

/*  a,    ,     */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/*     */
img {
  max-width: 100%;
  display: block;
}

/*        ArticlePage*/
article > * + * {
  margin-top: 1em;
}

/*       */
input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
}

button {
  padding: 0;
  margin: 0;
  background: none;
  border: 0;
}